.stars {
  display: flex;

  &__star {
    width: 16px;
    height: 16px;
    background: url(../images/star.webp) no-repeat center;
  }

  &__star:not(:last-child) {
    margin-right: 4px;
  }
}

.stars--1 :first-child,
.stars--2 :nth-child(-n+2),
.stars--3 :nth-child(-n+3),
.stars--4 :nth-child(-n+4),
.stars--5 :nth-child(-n+5) {
  background: url(../images/star-active.webp) no-repeat center;
}
